@import url('https://fonts.googleapis.com/css2?family=Ibarra+Real+Nova&family=Rajdhani&display=swap');

.home-page {
    // background-image: url(../images/SVG/pixabay.svg);//Photo by Pixabay: https://www.pexels.com/photo/abstract-business-code-coder-270348/
    background-repeat: no-repeat;
    background-size: cover;
    border-bottom-left-radius: 25px;
    border-bottom-right-radius: 25px;
    // color: #fff;
    // border: 3px solid blue;
    h2{
        font-size: 2rem;
    }
}

.center-logo {
    width: 200px;
}

.intro-text{
    p{
        margin: auto;
        width: 80%;
    }
}

.explore {
    font-size: 2rem;
    h3{
     font-size: 2rem; 
     margin-bottom: 1.5rem;  
    }
}

.chevrons-down {
    // border: 2px solid red;
    display: flex;
    flex-direction: column;
    a{
        position: relative;
    }
    span { 
        position: absolute;
        left: auto;
        right: auto;
    }
    .chev-1 {
        animation: move 1s infinite linear;
        bottom: -16px;
    }
    .chev-2 {
        animation: move 1s infinite linear;
        bottom: -35px;
    }
}

@keyframes move{
    0%{margin-bottom:0;}
    50%{margin-bottom:5px;}
    100%{margin-bottom:0;}
  }



@media screen and (min-width: 768px){
    .center-logo {
        width: 350px;
    }

    .intro-text{
        p{
            width: 50%;
        }
    }
}